
#education .link {
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 13px;
    font-weight: 500;
    text-decoration: none;
    color: #267cb9;
  
    }
  
    #education .area {
      font-weight: 600;
      font-size: 15px;
      color: #393939;
      text-decoration: none;
      margin-top: 1.5em; 
      margin-bottom: 0.5em;
      }
  
    #education .linkexp {
      margin-top: 10px;
      margin-bottom: 20px;
      font-size: 13px;
      font-weight: 400;
      text-decoration: none;
      color: #00a99d;
      }
  
      #education .linkdesc {
        font-size: 13px;
        font-weight: 500;
        text-decoration: none;
        color: #417be7;     
      }
  
      #education span p a {
        font-weight: 600;
        font-size: 14px;
        color: #393939;
        text-decoration: none;
        cursor: pointer; 
      }
  
    #education p {
      font-weight: 900;
      font-size: 14px;
      color: #727272;
      text-decoration: none;
      margin-top: 1.5em; 
      margin-bottom: 0.25em; 
  
    }
  
    #education span p {
      font-weight: 400;
      font-size: 14px;
      color: #727272;
      text-decoration: none;
      font-style: inherit;
      margin-top: 0.5em; 
      margin-bottom: .15em;
  
    }
  
    #education p img {
      width: 75%;
      margin-top:10px; 
      margin-bottom:10px; 
  
     }
     
     #education .ico {
      width: 60%;
      margin-top:10px; 
      margin-bottom:10px; 
  
     }
  
        
     #education .icos {
      width: 30%;
      margin-top:10px; 
      margin-bottom:10px; 
  
     }
    
    